import{
    postJsonRequest,
    postRequest,
    getRequest,
    putJsonRequest,
    deleteRequest
} from '@/api/axios'
import cookie from 'js-cookie';
export const getUserInfo=(params)=>{
    return getRequest('api/'+cookie.get("type")+'/user');
}
export const getBanner=(params)=>{
    return getRequest('api/image/image');
}
export const getIcon=(params)=>{
    return getRequest('api/image/imageLogo');
}
export const getNotice=(params)=>{
    return getRequest('api/image/imageTitle');
}
export const updatePwd=(params)=>{
    return postJsonRequest('api/user/updatePass',params);
}


